export default {"name":"沩山毛尖","img":"\\huangcha\\weishanmaojian\\img_201911221016429hca3d.jpg","desc":["沩山毛尖，湖南省宁乡县特产，中国国家地理标志产品。沩山毛尖成茶外形微卷成块状，色泽黄亮油润，白毫显露，汤色橙黄透亮，松烟香气芬芳浓郁，滋味醇甜爽口。叶底黄亮嫩匀，颇受边疆人民喜爱。"],"menus":[{"name":"沩山毛尖茶叶历史","content":[{"text":"沩山产茶历史悠久，远在唐代就已著称于世，清同治年间(1862～1874)《宁乡县志》载：“沩山茶，雨前采摘，香嫩清醇，不让武夷、龙井。商品销甘肃、新疆等省，久获厚利，密印寺院内数株味尤佳……”“文革”中发现密印寺大佛像体内存有茶叶三十余斤，可见茶叶在佛祖中的地位，是“茶佛一味”的又一生动见证。"}]},{"name":"沩山毛尖茶叶特点","content":[{"text":"沩山毛尖茶树饱受雨露滋润，故而根深叶茂，梗壮芽肥，茸毛多，持嫩性强，是制作名茶的最佳原料。制作后的茶叶，叶缘微卷，呈片状，形似兰花，色泽黄亮光润，身披白毫;冲泡后内质、汤色橙黄鲜亮，烟香浓厚，滋味醇甜爽口，风格独特。"},{"src":"\\huangcha\\weishanmaojian\\img_20191122101642hoyph0.jpg"}]},{"name":"沩山毛尖加工工艺","content":[{"text":"鲜叶摊青→杀青→初揉→闷黄→初烘→复揉→二次闷黄→足烘"},{"text":"摊青：摊放厚度≤10厘米，时间为6小时—8小时;"},{"text":"杀青：温度为260℃—280℃。要求杀匀杀透，叶色呈暗绿色，手捏略粘手。"},{"src":"\\huangcha\\weishanmaojian\\img_20191122101642uoqbgz.jpg"},{"text":"闷黄：温度为38℃—42℃，时间为12分—15分;"},{"text":"初烘：温度为100℃—120℃，烘至6成干，摊凉;"},{"text":"二次闷黄：温度为38℃—42℃，时间为8小时—10小时;"},{"text":"足烘：温度为80℃—90℃，分2次，每次20分。"}]},{"name":"沩山毛尖储藏方法","content":[{"text":"1、沩山毛尖瓦罐储茶法"},{"text":"此法古代就有.明人冯梦祯《快雪堂漫录》云：“实茶大瓮，底置箬，封固倒放，则过夏不黄，以其气不外泄也。”茶叶含水量不能超过6%.可用生石灰除湿.具体操作方法同石灰块保存法。"},{"text":"2、沩山毛尖热水瓶的储藏法"},{"text":"选用保暖性良好的热水瓶作盛具。将干燥的茶叶装入瓶内，装实装足，尽量减少空气存留量，瓶口用软木塞盖紧，塞缘涂白蜡封口，再裹以胶布。由于瓶内空气少，温度稳定，这种方法保持效果也较好，且简便易行。"},{"text":"3、沩山毛尖食品袋储藏法"},{"text":"先用洁净无异味白纸包好沩山毛尖茶叶，再包上一张牛皮纸，然后装入一只无空隙的塑料食品袋内，轻轻挤压，将袋内空气挤出，随即用细软绳子扎紧袋口取一只塑料食品袋，反套在第一只袋外面，同样轻轻挤压，将袋内空气挤压在用绳子扎紧口袋，最后把它放进干燥无味的密闭的铁桶内。"}]},{"name":"沩山毛尖冲泡方法","content":[{"text":"1、赏茶：外形条索紧细、圆、光、直，白毫显露，有峰苗，色泽隐绿，油润光滑。"},{"text":"2、烫杯：用壶里的热水采用回旋斟水法浸润茶杯，提高茶杯的温度。"},{"src":"\\huangcha\\weishanmaojian\\img_201911221016423h0pua.jpg"},{"text":"3、注水：水注到杯身的十分之七满，注水时注意水的温度要达到90度(这样才能在投茶时使水温在85度)。水用山泉水为最佳。"},{"text":"4、投茶：特级毛尖采用上投法。用茶匙把茶荷中的毛尖均匀拨到玻璃杯中去。"},{"text":"5、静置：等待茶叶吸水下沉慢慢展开(浸泡时间为3～5分钟)。"},{"text":"6、品茶：毛尖有烟松香，汤色鲜绿，滋味鲜醇，叶底嫩绿匀整。"}]},{"name":"沩山毛尖功效作用","content":[{"text":"一、抗辐射。"},{"text":"绿茶\n        类的最大好处就是能够抵抗电脑及手机等电子产品对人体所发出的辐射伤害。这是由于毛尖茶中含有的茶多酚及氧化物能够吸收放射性物质的能力。饮茶减少了血多因辐射而引发的一些病症。"},{"text":"二、美白肌肤。"},{"text":"喝茶能减少体内的黑色素，使我们的肌肤光鲜亮丽。毛尖茶本身就含有对美白有好处的维生素C。其还含有的类黄酮能够增强维生素C的抗氧化能力。"},{"text":"三、清烟。"},{"text":"瘾君子们的烟瘾都是很大的，烟多了存在体内的尼古丁就会越来越多，对人体造成伤害。多喝毛尖茶能够减少并排除体内的尼古丁。"},{"text":"四、消暑解渴清热。"},{"text":"夏天饮毛尖茶是最好的时刻。其茶水中含有的糖类及氨基酸物质能够对口腔中的唾液起到化学反应。咖啡因能够促使毛孔张开散出热能量。"},{"text":"五、常喝沩山毛尖茶的功效能降低血压。"},{"text":"是因为沩山毛尖茶叶内含的咖啡碱和儿茶素能促使人体血管壁松弛，并能增血管有效直径，使血管壁保持一定弹性，消除脉管痉挛。"}]},{"name":"沩山毛尖获奖情况","content":[{"text":"1986年荣获名优证书;"},{"text":"1988年获中国首届食品博览会铜牌奖。"}]},{"name":"沩山毛尖鉴别好茶","content":[{"text":"观外形"},{"text":"正宗沩山毛尖茶干茶色泽绿润、条索紧细卷曲有锋苗、白毫满布，闻之茶香飘逸、鲜爽清新。"},{"text":"品茶汤"},{"text":"上乘沩山毛尖茶冲泡后，茶汤黄亮油润，香气嫩香持久，滋味鲜爽，回味甘甜。"},{"text":"审叶底"},{"text":"沩山毛尖茶的原料是在清明前后采摘的第一叶初展的细嫩芽头，经冲泡后，叶底仍现芽叶，细嫩均匀，柔软鲜活。"}]}]}